<template>
    <div id="proposalApply" style="{ width: screenWidth + 'px' }"  >
       
            <el-form ref="ruleForm" :model="ruleForm" :rules="rules" label-width="100px" >
                <el-form-item  style="margin-left: 140px;margin-top:20px;margin-bottom:40px">
                    <span style="font-size:20px">GCWE</span>  
                                
                </el-form-item>
                <!-- <el-form-item  style="margin-left: 300px;margin-top:20px">
                               
                </el-form-item> -->
                <el-form-item  label="" >
                    <el-form-item style="margin-left:20px">
                    {{ruleForm.proposalCode}}
                    </el-form-item>         
                </el-form-item>
                <el-form-item label="" required style="margin-left:330px;margin-top:-60px;">  
                    <el-form-item style="margin-left:30px">                  
                        {{returnTime(new Date)}}
                    </el-form-item> 
                </el-form-item>
                <el-form-item label="" prop="proposalPerson"  style="margin-top: 40px;">
                    <el-input  style="width:200px;margin-left:20px" v-model="ruleForm.proposalPerson"></el-input>
                </el-form-item>
                
                <!-- <el-form-item label=""    style="margin-left:340px;margin-top:-60px;" required prop="proposalDate">
                    <el-date-picker value-format="yyyy-MM-dd" v-model="ruleForm.proposalDate" type="date" placeholder=""  style="width:200px;margin-left:20px" ></el-date-picker>
                </el-form-item>   -->
                
                <el-form-item label=""  prop="proposalName" style="margin-left:330px;margin-top:-60px;">
                <el-input  style="width:200px;margin-left:30px" v-model="ruleForm.proposalName"></el-input>
                </el-form-item>

                <el-form-item label=""  prop="companyId">
                    <el-select v-model="ruleForm.companyId" placeholder="" clearable style="width:200px;margin-left:20px" @change="selectDepartmentByCompanyId">
                        <el-option v-for="item in searchCompanys" :key="item.companyId" :label="item.companyAlias" :value="item.companyId"> </el-option>
                    </el-select>
                </el-form-item>
           
                <el-form-item label="" prop="departmentId" style="margin-left:330px;margin-top:-60px;">
                    <el-select v-model="ruleForm.departmentId" placeholder="" clearable style="width:200px;margin-left:30px" >
                        <el-option v-for="item in searchDepartments" :key="item.departmentId" :label="item.departmentName" :value="item.departmentId"> </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="" prop="beforeImprovement" >
                    <el-input type="textarea"  v-model=ruleForm.beforeImprovement  style="width:420px;margin-left:20px"></el-input>
                    <el-button type="success" @click="dialogVisible2 = true" style="margin-left: 10px;"></el-button> 
                </el-form-item>
            
                <el-form-item label="" prop="afterImprovement" >
                    <el-input type="textarea"  v-model=ruleForm.afterImprovement  style="width:420px;margin-left:20px"></el-input>
                    <el-button type="success" @click="dialogVisible3 = true" style="margin-left: 10px;"></el-button> 
                </el-form-item>

                <el-form-item label=""  style="margin-top: 20px;" prop="saveMoney" >
                    <el-input v-model.number=ruleForm.saveMoney  style="width:150px;margin-left:20px" ></el-input><span style="margin-left:20px"></span>
                </el-form-item>

                <el-form-item label=""  style="margin-left:340px;margin-top:-60px;" prop="saveTime"> 
                    <el-input v-model.number=ruleForm.saveTime style="width:150px;margin-left:20px" ></el-input><span style="margin-left:20px"></span>
                </el-form-item>
                
                <el-form-item style="margin-left:120px;margin-top:50px">
                    <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                    <el-button @click="resetForm('ruleForm')" style="margin-left: 120px"></el-button>
                </el-form-item>
            </el-form>
      
       
        <!--  -->
        <el-dialog title="" :visible.sync="dialogVisible2" width="40%">
            <el-form :model="form">
                <el-form-item :label-width="formLabelWidth" ref="uploadElement">
                <el-upload
                    ref="upload"
                    :action="uploadUrl"
                    list-type="picture-card"
                    name="load"
                    accept="image/png,image/jpeg"
                    :limit="limitNum"
                    :before-upload="handleBeforeUpload"
                    :on-preview="handlePictureCardPreview"
                    :on-remove="handleRemove"
                    :on-success="success"
                    :class="{ hide: hideUpload }"
                >
                    <i class="el-icon-plus"></i>
                </el-upload>
                <el-dialog :visible.sync="dialogVisible">
                    <img width="100%" :src="dialogImageUrl" alt="" />
                </el-dialog>
                </el-form-item>
                <el-form-item style="margin-left:200px;">
                <!-- <el-button size="small" type="primary" @click="submitImage"
                    ></el-button
                > -->
                <el-button size="small" type="primary" @click="cancel" style="width:80px;"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
      
       <!--  -->
        <el-dialog title="" :visible.sync="dialogVisible3" width="40%">
            <el-form :model="form">
                <el-form-item :label-width="formLabelWidth" ref="uploadElement">
                <el-upload
                    ref="upload"
                    :action="uploadUrl"
                    list-type="picture-card"
                    name="load"
                    accept="image/png,image/jpeg"
                    :limit="limitNum"
                    :before-upload="handleBeforeUpload"
                    :on-preview="handlePictureCardPreview2"
                    :on-remove="handleRemove"
                    :on-success="success2"
                    :class="{ hide: hideUpload }"
                >
                    <i class="el-icon-plus"></i>
                </el-upload>
                <el-dialog :visible.sync="dialogVisible">
                    <img width="100%" :src="dialogImageUrl2" alt="" />
                </el-dialog>
                </el-form-item>
                <el-form-item style="margin-left:200px;">
                <!-- <el-button size="small" type="primary" @click="submitImage"
                    ></el-button
                > -->
                <el-button size="small" type="primary" @click="cancel" style="width:80px;"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>
<script>
    // import SessionTools from '../util/SessionTools';
    import ajax from "../util/myAxios";
    // import axios from 'axios';
    export default {
    name: 'ProposalApply',
    data() {
      return {
        screenWidth:document.documentElement.clientWidth,
        // labelPosition: "right",
        // formLabelAlign: {},
        // disabledIput: true,
        // hideUpload: false,
        // dialogImageUrl: "",
        // dialogVisible: false, //
        // formLabelWidth: "80px",
        // limitNum: 1,
        // form: {},
        // dialogVisible2: false, //
        // dialogVisiblePwd: false,
        // uploadUrl: this.$store.state.baseURL + "/doUpload",
       
        limitNum: 1,
        form: {},
        formLabelAlign:{
            proposalImage:'',
            proposalImage2:'',
        },
        formLabelWidth: "80px",
         hideUpload: false,
        dialogVisible:false,
        dialogVisible2:false,
        dialogVisible3:false,
        dialogImageUrl: '',
        dialogImageUrl2: '',
        uploadUrl: this.$store.state.baseURL + "/doUpload",
        // uploadUrl:"http://localhost:11001//doUpload",
        // conTop: {
        //     backgroundImage:'url(' + require('../img/background.jpeg') + ')',
        //     width:"100%",
        //     height:"100%",
        //     backgroundRepeat: "no-repeat",
        //     backgroundPosition: "center center",
        // },
        ruleForm: {
           proposalId:'',
            //
            proposalPerson:'',
            proposalName:'',
            // proposalDate:'',
            companyId:'',
            departmentId:'',
            beforeImprovement:'',
            afterImprovement:'',
            saveMoney:'',//
            saveTime:'',//
            proposalCode:'',//
        },
        rules: {
            beforeImprovement: [
                { required: true, message: '', trigger: 'blur' },
                // { min: 10, max: 100, message: ' 10  100 ', trigger: 'blur' }
            ],
            afterImprovement: [
                { required: true, message: '', trigger: 'blur' },
                // { min: 10, max: 100, message: ' 10  100 ', trigger: 'blur' }
            ],
            proposalPerson: [
                { required: true, message: '', trigger: 'blur' },
                { min: 2, max: 5, message: ' 2  5 ', trigger: 'blur' }
            ],
            proposalName: [
                { required: true, message: '', trigger: 'blur' },
                { min: 1, max: 10, message: ' 1  10 ', trigger: 'blur' }
            ],
            proposalDate: [
                { type: 'date', required: true, message: '', trigger: 'change' }
            ],
            companyId: [
                { required: true, message: '', trigger: 'change' }
            ],
            departmentId: [
                { required: true, message: '', trigger: 'change' }
            ],
            saveMoney: [
                { required: true, message: '', trigger: 'blur'},
                { type: 'number', message: ''}
            ],
            saveTime: [
                { required: true, message: ''},
                { type: 'number', message: ''}
            ],
        },
        // pickerOptions: {
        //     // 
        //     disabledDate: time => {
        //         return (
        //             time.getTime() < new Date( new Date()).getTime()
        //         ); 
        //     }
        // },
      
        //
        searchCompanys:[],
        searchDepartments:[],
      
      }
    },
    created(){
        console.log("screenWidth=="+this.screenWidth);
        // window.addEventListener('resize', this.handleResize)
    },
    

    // created() {
    //     //html
    //     var html = document.getElementsByTagName("html")[0];
    //     if (window.innerWidth < 750) {
    //         ///750*100750px750750pxhtml100pxp{font-size: 1rem}100px375pxhtml50pxp50pxrem
    //         html.style.fontSize = (window.innerWidth / 750) * 100 + "px";
    //         } else {
    //         html.style.fontSize = "16px";
    //         }
    //         //html
    //         window.addEventListener("resize", function () {
    //         if (window.innerWidth < 750) {
    //             html.style.fontSize = (window.innerWidth / 750) * 100 + "px";
    //             // console.log(window.innerWidth/750*100 + "px");
    //         } else {
    //             html.style.fontSize = "16px";
    //         }
    //     });
    // },
    mounted() {

        // this.adjustFontsize();
        this.getAllCompany();
        this.getProposalCode();

        
        window.nresize= () => {
        return (() => {
            window.screenWidth = document.body.clientWidth;
            this.screenWidth = window.screenWidth;
        })();

        };
    },
    // destroyed(){
    //     window.removeEventListener('resize', this.getHeight)
    // },
    watch:{
        screenHeight (val) {
        // resize
            if (!this.timer) {
                // screenWidthdatascreenWidth
                this.screenHeight = val
                this.timer = true
                let that = this
                setTimeout(function () {
                // screenWidth
                    console.log(that.screenHeight)
                    that.timer = false
                }, 400)
            }
        }
    },
    // created() {
    //     //html
    //     var html = document.getElementsByTagName("html")[0];
    //     if (window.innerWidth < 750) {
    //         ///750*100750px750750pxhtml100pxp{font-size: 1rem}100px375pxhtml50pxp50pxrem
    //         html.style.fontSize = (window.innerWidth / 750) * 100 + "px";
    //         } else {
    //         html.style.fontSize = "16px";
    //         }
    //         //html
    //         window.addEventListener("resize", function () {
    //         if (window.innerWidth < 750) {
    //             html.style.fontSize = (window.innerWidth / 750) * 100 + "px";
    //             // console.log(window.innerWidth/750*100 + "px");
    //         } else {
    //             html.style.fontSize = "16px";
    //         }
    //     });
    // },
    methods: {
         handleResize () {
            this.fullWidth = document.documentElement.clientWidth
        },
        // getHeight(){
        //     this.height = window.innerHeight - 70 + 'px';
        // },
        // adjustFontsize() {
        //     var n = document.documentElement,
        //     e = screen.width > 0 && (window.innerWidth >= screen.width || 0 == window.innerWidth) ? screen.width: window.innerWidth;
        //     e && (e = Math.min(e, 750), n.style.fontSize = e / 375 * 20 + "px")
        // },
       
        // BlurText(e){
        //     let boolean =new RegExp("^[1-9][0-9]*$").test(e.ruleForm.saveMoney.value);
        //     if(!boolean){
        //         this.$message.warning('');
        //         e.ruleForm.saveMoney.value='';
        //     }
        // },
     
        // 
        getAllCompany() {
            ajax({
                type:'get',
                url:'/getAllCompany',
                data:{
                    
                },
                success:(resp)=>{
                    console.log('getAllCompany',resp);
                    this.searchCompanys=resp.list;
                }
            })
        }, 
         // 
        selectDepartmentByCompanyId() {
            ajax({
                type:'get',
                url:'/selectDepartmentByCompanyId',
                data:{
                   companyId:this.ruleForm.companyId,
                },
                success:(resp)=>{
                    console.log('selectDepartmentByCompanyId',resp);
                    this.searchDepartments=resp.list;
                    
                }
            })
        }, 
        //
        getProposalCode() {
            
            ajax({
                type:'get',
                url:'/getProposalCode',
                data:{
           
                },
                success:(resp)=>{
                    console.log('getProposalCode',resp.message);
                    this.ruleForm.proposalCode=resp.message;
                }
            })
        }, 
        //
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    console.log('submit!');
                    console.log("proposalPerson=="+this.ruleForm.proposalPerson);
                    console.log("proposalName=="+this.ruleForm.proposalName);
                    // console.log("proposalDate=="+this.ruleForm.proposalDate);
                    console.log("companyId=="+this.ruleForm.companyId);
                    console.log("departmentId=="+this.ruleForm.departmentId);
                    console.log("beforeImprovement=="+this.ruleForm.beforeImprovement);
                    console.log("afterImprovement=="+this.ruleForm.afterImprovement);
                    console.log("saveMoney=="+this.ruleForm.saveMoney);
                    console.log("saveTime=="+this.ruleForm.saveTime);
                    let match=new RegExp("^[1-9][0-9]*$");
                    if(match.test(this.ruleForm.saveMoney)==true&&match.test(this.ruleForm.saveTime)==true){
                        ajax({
                            type:'post',
                            url:'/addProposal',
                            data:{
                                proposalPerson:this.ruleForm.proposalPerson,
                                proposalName:this.ruleForm.proposalName,
                                gmtCreate: this.returnTime(new Date),
                                companyId: this.ruleForm.companyId,
                                departmentId:this.ruleForm.departmentId,
                                beforeImprovement:this.ruleForm.beforeImprovement,
                                afterImprovement:this.ruleForm.afterImprovement,
                                saveMoney:this.ruleForm.saveMoney,
                                saveTime:this.ruleForm.saveTime,
                                proposalCode:this.ruleForm.proposalCode,
                            },
                            success:(resp)=>{
                                console.log('addProposal',resp);                 
                                this.$message(resp.message) ;    
                                this.getProposalCode();   
                                this.resetForm('ruleForm');
                                console.log('proposalImage',this.formLabelAlign.proposalImage); 
                                console.log('proposalImage2',this.formLabelAlign.proposalImage2); 
                                ajax({
                                    type: "post",
                                    url: "/updateImage",
                                    data: {
                                        proposalImage: this.formLabelAlign.proposalImage,
                                        proposalImage2: this.formLabelAlign.proposalImage2,
                                        proposalCode: this.ruleForm.proposalCode,
                                    },
                                    withCredentials: true,
                                    success: (resp) => {
                                         console.log(resp.message);
                                        // this.dialogVisible2 = !this.dialogVisible2;
                                        // this.initPage();
                                    },
                                });               
                            }
                        })                       
                    }else{
                        this.$message.warning('');
                        this.getProposalCode();   
                        this.resetForm('ruleForm'); 
                    }                  
                }
                else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
       
        //
        resetForm(formName) {
            this.$refs[formName].resetFields();
            console.log(1111);
        },
       
        //
        returnTime(time2){
            var time=new Date(time2);
            return time.toLocaleDateString();
        },
       
        //
        //
        success(file) {
            this.$message(file.message);
            var fileUrl = file.obj;
            this.formLabelAlign.proposalImage = fileUrl;
            // this.dialogVisible2=false;
            console.log("fileUrl=="+this.formLabelAlign.proposalImage);
        },
        //
        success2(file) {
            this.$message(file.message);
            var fileUrl = file.obj;
            this.formLabelAlign.proposalImage2 = fileUrl;
            // this.dialogVisible2=false;
            console.log("fileUrl2=="+this.formLabelAlign.proposalImage2);
        },
        //
        handleBeforeUpload(file) {
            const isLt10M = file.size / 1024 / 1024 < 10;
            if (!isLt10M) {
                this.$message.error(' 10MB!');
            }
            return isLt10M;
        },
        // 
        handleRemove(file, fileList) {
            this.hideUpload = fileList.length >= this.limitNum;
        },
        // 
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
         // 
        handlePictureCardPreview2(file) {
            this.dialogImageUrl2 = file.url;
            this.dialogVisible = true;
        },
        cancel() {
            this.dialogVisible2 = false;
            this.dialogVisible3 = false;
        },
        // uploadFile() {
        //     this.$refs.upload.submit();
        // },  
     
    },
  }
</script>

